.input-wrapper {
  position: relative;
}

.input {
  padding: 10px;
  font-size: 14px;
  height: 52px;
  width: 100%;
  border: 4px solid var(--base-fade-color);
  border-radius: 0;
}

.input:focus {
  outline: none;
  border-color: var(--base-color);
}

.input-wrapper__affix {
  top: 0;
  right: 0;
  height: 100%;
  display: flex;
  padding-left: 5px;
  padding-right: 5px;
  align-items: center;
  position: absolute;
  width: 50px;
  color: #000;
  font-weight: bold;
}

.input--with-affix {
  padding-right: 50px;
}
