.hire-section {
}

.hire-form {
  margin-top: 20px;
}

.hire-form__label {
  margin-bottom: 10px;
}

.hire-form__inputs {
  display: flex;
  margin-bottom: 20px;
  gap: 20px;
}

.hire-form__inputs .input-wrapper {
  flex: 1;
  min-width: 150px;
}

.hire-form__inputs .button {
  min-width: 150px;
}

.hire-form__predict-calcs {
  text-align: center;
}

.hire-form__minig-not-initialised {
  text-align: center;
  margin: 10px auto;
}

.hire-form__error-message {
  text-align: center;
  color: crimson;
  margin-bottom: 20px;
}
